import * as React from 'react';
import vkBridge from '@vkontakte/vk-bridge';
import type { AppearanceType } from '../../lib/appearance';
import { platform, PlatformType } from '../../lib/platform';

/**
 * TODO [>=6]:  enum (#5049).
 *
 * @deprecated v5.8.0
 */
export enum WebviewType {
  VKAPPS = 'vkapps',
  INTERNAL = 'internal',
}

export interface ConfigProviderContextInterface {
  /**
   *  ,   `ConfigProvider`,   :  webview    .
   *
   *   :
   * - `isWebView={true}`
   * - platform="ios"
   *
   *   `View`     .
   *
   *     `bridge.isWebView()`.
   *
   * >   **v6**     `@vkontakte/vk-bridge` (. https://github.com/VKCOM/VKUI/issues/5049).
   * >    `@vkontakte/vk-bridge`,   `bridge.isWebView()`  .
   */
  isWebView: boolean; // TODO [>=6]:    v6 @vkontakte/vk-bridge 
  /**
   *  .<br>
   *   `WebviewType.VKAPPS`        Mini Apps (     )
   *
   * >   **v6**    (. https://github.com/VKCOM/VKUI/issues/5049).
   * >
   * >     :
   * >
   * > - `webviewType={WebviewType.INTERNAL}` -> `hasCustomPanelHeaderAfter={false}`.
   * > - `webviewType={WebviewType.VKAPPS}` -> `hasCustomPanelHeaderAfter={true}`.    `customPanelHeaderAfterMinWidth={<value>}` (   `90`).
   *
   * @deprecated v5.8.0
   */
  webviewType?: WebviewType.INTERNAL | WebviewType.VKAPPS;
  /**
   *  `true`  `after`  `PanelHeader`    
   * ""  (,   webview).
   *
   * > Note:     `PanelHeader`   , 
   * > .
   *
   * >   **v6**     `false` (. https://github.com/VKCOM/VKUI/issues/5049).
   */
  hasCustomPanelHeaderAfter: boolean;
  /**
   *      `after`  `PanelHeader`  
   * ""  (,    webview).
   *
   *    `hasCustomPanelHeaderAfter={true}` (.  `hasCustomPanelHeaderAfter`).
   *
   * @default 90
   */
  customPanelHeaderAfterMinWidth: number | string;
  /**
   *     `light`  `dark`
   */
  appearance: AppearanceType | undefined;
  /**
   *        `Root`  `View`
   */
  transitionMotionEnabled: boolean;
  /**
   * 
   */
  platform: PlatformType;
  /**
   *     BCP 47
   */
  locale: string;
}

export const ConfigProviderContext = React.createContext<ConfigProviderContextInterface>({
  // TODO [>=6]:   (#5049).
  webviewType: undefined,
  // TODO [>=6]:    `false` (#5049).
  hasCustomPanelHeaderAfter: true,
  customPanelHeaderAfterMinWidth: 90,
  // TODO [>=6]:   vkBridge.  `false`   (#5049).
  isWebView: vkBridge.isWebView(),
  transitionMotionEnabled: true,
  platform: platform(),
  appearance: undefined, // undefined      
  locale: 'ru',
});

export const useConfigProvider = () => React.useContext(ConfigProviderContext);
